@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:ital,wght@1,900&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

Navbar{font-size:90px}
h1{padding-left:13%;font-size:4rem}Table{text-align:center}.Children{padding-left:2rem}.terms{position:absolute;left:70%;padding-left:2rem}@media(max-width: 600px){.terms{left:40%;width:100%}}
.product{padding-left:200px;padding-right:200px;-webkit-justify-content:space-between;justify-content:space-between;overflow:hidden}.product__images{cursor:pointer;margin-bottom:20px;-webkit-transform:scale(1);transform:scale(1);transition:.3s ease-in-out;overflow:hidden}.product__images:hover{overflow:hidden;-webkit-transform:scale(1.1);transform:scale(1.1)}@media(max-width: 600px){.product{padding-left:30px;padding-right:30px}.product__images{-webkit-align-items:center;align-items:center}}
.ourwork{margin-top:100px}
.About{font-family:"Bebas Neue",cursive;padding-left:3rem;padding-right:3rem}.About__owner{font-weight:bold;display:-webkit-flex;display:flex;-webkit-justify-content:space-between;justify-content:space-between}.About__content{text-align:justify;text-justify:inter-word;margin-top:2rem;margin-left:2rem}.About__review--header{margin-top:2rem;margin-bottom:2rem}@media only screen and (max-width: 600px){.About__image{width:30%}.About__review--header{padding-left:30%}}.card{font-weight:bold}
.products_header{font-size:5rem;padding-left:40%}.container{padding:0 20%}.view__button{margin-top:3%;margin-left:40%;border-radius:10rem;transition:all .2s;text-transform:uppercase;text-decoration:none;font-weight:bold;font-size:1.6rem;border:none;padding:2.5rem 5rem;cursor:pointer;background-color:#000;color:#fff}.view__images{margin-left:20%;margin-right:10%}.image_view{margin-right:2%;-webkit-transform:scale(1);transform:scale(1);transition:.3s ease-in-out;overflow:hidden}.image_view:hover{overflow:hidden;-webkit-transform:scale(1.1);transform:scale(1.1)}@media(max-width: 600px){.products_header{padding-left:10%}.view__button{margin-left:30%}}
hr{border:1px solid #1a0d00}.header{height:100vh;width:100%;background-color:#fff;background-size:cover;background-position:left;position:relative}.header__image{background-image:url(/hitcreation/static/media/priya1.90869e7f.png);width:100%;height:100%;background-size:cover}@media(max-width: 600px){.header__image{background:url(/hitcreation/static/media/background_phone.6ad06870.png);background-size:cover}}.header__main{position:absolute;width:100%;height:20%;top:80%;left:15%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);margin-top:6rem}.header__main--title{background-color:#3f0d12;background:-webkit-linear-gradient(315deg, #3f0d12 0%, #a71d31 74%);-webkit-background-clip:text;-webkit-text-fill-color:transparent;font-weight:bold;letter-spacing:6px;font-size:5rem}.header__main--subtitle{position:absolute;left:60%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);text-align:center;font-size:20px;letter-spacing:5.5px;padding-top:.5rem;padding-bottom:.5rem;background-color:#3f0d12;background-image:linear-gradient(315deg, #3f0d12 0%, #a71d31 74%);text-transform:uppercase;margin-top:2rem;margin-bottom:10rem;color:#fff;height:30%}@media(max-width: 600px){.header__main{top:60%}.header__main--subtitle{width:70%;left:70%;height:50%}}.features{color:#fff;background-color:#3f0d12;background-image:linear-gradient(315deg, #3f0d12 0%, #a71d31 74%);padding:3rem 0 5rem 0;background-size:cover}.features__container{display:grid;grid-template-columns:auto auto auto;grid-column-gap:12rem;-webkit-column-gap:12rem;column-gap:12rem;-webkit-justify-content:center;justify-content:center}.features__title{text-align:center;padding-bottom:5rem;font-family:"Playfair Display",serif;font-size:3rem}.features__title--header{padding-top:8rem;font-size:7rem}.features__title--footer{font-size:3.8rem}.features__col{height:100%;text-align:center;border:2px solid #fff;width:25rem;padding:4rem;border-radius:10px}.features__col--title{padding-bottom:2rem}.features__col--logo-green{padding-bottom:2rem;color:#fff;color:#fff}.features__col--logo-orange{padding-bottom:2rem;color:#fff}ion-icon{font-size:40px}@media(max-width: 550px){.features__container{padding-left:25%;display:grid;grid-template-columns:1fr;grid-row-gap:60px;row-gap:60px;margin:0 auto}}.booknow{padding:10rem 0}.booknow__head{font-size:4rem;text-align:center;color:#3f0d12}.booknow__container{width:70%;margin:5rem auto;grid-row-gap:2rem;row-gap:2rem;border:3px solid #000;padding:3rem;border-radius:5px;position:relative;text-align:center}.booknow__input{padding:5px}.booknow__button{margin-left:5rem;border-radius:10rem;transition:all .2s;text-transform:uppercase;text-decoration:none;font-weight:bold;font-size:1.6rem;border:none;padding:2.5rem 5rem;cursor:pointer;background-color:#000;color:#fff}h1{padding-left:13%;font-size:4rem}Table{text-align:center}.Children{padding-left:2rem}.terms{position:absolute;left:70%;padding-left:2rem}@media(max-width: 600px){.terms{left:40%;width:100%}}*,*::before,*::after{margin:0;padding:0;box-sizing:inherit}html{font-size:62.5%}body{font-size:2rem;box-sizing:border-box}*{font-family:"Karla",sans-serif}
